* {
  box-sizing: border-box;
  padding: 0;
  margin: 0; }

.Container {
  position: relative;
  width: 700px;
  padding-right: 250px;
  margin: 13px;
  min-height: 400px; }

.EmployeeList {
  padding-top: 13px; }

.Employee {
  padding: 0 10px 13px 0;
  border-bottom: 1px solid #ccc;
  margin-bottom: 13px;
  white-space: pre-line; }

.Editor {
  position: absolute;
  right: 0;
  top: 0;
  background: #ccc;
  width: 250px;
  min-height: 400px;
  padding: 13px; }

.ToggleEditor {
  position: absolute;
  right: 260px;
  top: 0; }

.EmployeeEdit {
  margin-bottom: 13px; }

.Input {
  width: 100%;
  height: 75px; }
